.restaurant-menu {
  font-family: Arial, sans-serif;
  padding: 20px;
  background-color: #f4f4f4;
}

.restaurant-summary {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
}

.restaurant-img {
  width: 200px;
  height: 200px;
  border-radius: 10px;
  margin-right: 20px;
  object-fit: cover;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
}

.restaurant-summary-details {
  flex-grow: 1;
}

.restaurant-title {
  font-size: 24px;
  margin-bottom: 10px;
  color: #333;
}

.restaurant-tags {
  color: #777;
  margin-bottom: 10px;
}

.restaurant-details {
  display: flex;
  align-items: center;
  color: #555;
  font-size: 16px;
}

.restaurant-rating {
  display: flex;
  align-items: center;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
}

.restaurant-rating i {
  margin-right: 5px;
}

.restaurant-rating-slash {
  margin: 0 10px;
  color: #555;
}

.restaurant-menu-content {
  margin-top: 30px;
}

.menu-title-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.menu-title {
  font-size: 22px;
  color: #333;
}

.menu-count {
  color: #777;
}

.menu-items-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.menu-item {
  border: 1px solid #e0e0e0;
  border-radius: 10px;
  overflow: hidden;
  transition: transform 0.3s ease;
  background-color: #fff;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
}

.menu-item:hover {
  transform: translateY(-5px);
}

.menu-item-details {
  padding: 15px;
}

.item-title {
  font-size: 18px;
  margin-bottom: 10px;
  color: #333;
}

.item-cost {
  color: #007bff;
  font-weight: bold;
  margin-bottom: 10px;
}

.item-desc {
  color: #777;
}

.menu-img-wrapper {
  position: relative;
}

.menu-item-img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 10px 10px 0 0;
}

.add-btn {
  position: absolute;
  bottom: 10px;
  right: 10px;
